


const checkIbanNumber = async (iban) => {
        
    const location = `https://openiban.com/validate/${iban}?getBIC=true&validateBankCode=true`;
    const settings = {
        method: 'GET',
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        }
    };
    try {
        const fetchResponse = await fetch(location, settings);
        const data = await fetchResponse.json();
        console.log('data###', data)
        return data;
    } catch (e) {
        return console.log('error_', e);
    }    

}



export default checkIbanNumber